
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import {Routes,Route} from "react-router-dom";
import Home from './Pages/home';
import Blogs from './Pages/blogs';
import CreateBlog from './Pages/createblog';
import { BlogContext } from './Pages/BlogContext';
import { useState } from 'react';
import ReadBlogContent from './Pages/ReadBlogContent';
import DeleteBlogs from './Pages/deletePost';
import 'boxicons'

function App() {
  
  const [blog,setBlog]=useState([{title:"What's a Blog & Why You Need One",
  content:"Even if you‘re not sure what a blog is, you’ve no doubt come across one at some point in time. Perhaps you‘ve stumbled across a blog when you’ve searched “healthy dinner recipes”. In fact, if you're reading this, guess what? You're on a blog. (Very meta, I know.) <br/>The word blog is a combined version of the words “web” and “log.” At their inception, blogs were simply an online diary where people could keep a log about their daily lives on the web. They have since morphed into an essential forum for individuals and businesses alike to share information and updates.Different types of blogs cover varying topics, from food and fashion to marketing. Blogs are composed of individual posts on more specific subjects within the blog’s field of expertise. To get started with blogs on social media, check out microblogging. These posts often serve as a platform for discussions, as many blogs have active comments sections. You can think of a blog almost as a newspaper that adds articles and continually maintains the archive. "},
  {title:"How to Write a Blog Post: A Step-by-Step Guide " , content:"If you’ve ever read a blog post, you’ve consumed content from a thought leader that is an expert in their industry. Chances are if the blog post was written effectively, you came away with helpful knowledge and a positive opinion about the writer or brand that produced the content.Anyone can connect with their audience through blogging and enjoy the myriad benefits that blogging provides: organic traffic from search engines, promotional content for social media, and recognition from a new audience you haven’t tapped into yet.Anyone can connect with their audience through blogging and enjoy the myriad benefits that blogging provides: organic traffic from search engines, promotional content for social media, and recognition from a new audience you haven’t tapped into yet.A blog post is any article, news piece, or guide that's published in the blog section of a website. A blog post typically covers a specific topic or query, is educational in nature, ranges from 600 to 2,000+ words, and contains other media types such as images, videos, infographics, and interactive charts.Blog posts allow you and your business to publish insights, thoughts, and stories on your website about any topic. They can help you boost brand awareness, credibility, conversions, and revenue. Most importantly, they can help you drive traffic to your website."},
  { title:"How to optimize your blog’s posting schedule to gain traction", content:"The success of your blog heavily relies on a carefully thought-out schedule for posting. By defining when and how often you’ll publish content, you can efficiently plan and organize your writing process, saving time and reducing stress in the long run. The schedule ensures that your blog remains current and relevant, fostering consistency that attracts and retains a loyal following. Also, a well-maintained blogging schedule can significantly boost your search engine optimization (SEO) efforts since search engines reward sites with fresh and regularly published content. Blogs often serve as the foundation for maintaining that consistency and play a vital role in attracting new clients. They also give you the opportunity to address daily queries and concerns that clients may have. s a rule of thumb, higher blogging frequency tends to attract more traffic. Companies that publish four blog posts per week or more witness 3.5 times the traffic compared to those posting less than once a week. Blogging consistently can also significantly impact audience engagement and business growth."},
{title:"13 Tried and True Techniques to Write Better Blog Posts",content:"Want to write successful blog posts? Writing good blog posts has a big learning curve, but you can greatly shortcut the process using proven techniques. In this post, we’ll share 13 tried & true techniques to write better blog posts.Writing successful blog posts is a battle. A battle in the war waged between ideas worth sharing, and the vast array of online distractions.Writing successful blog posts is a battle. A battle in the war waged between ideas worth sharing, and the vast array of online distractions.Writing successful blog posts is a battle. A battle in the war waged between ideas worth sharing, and the vast array of online distractions."},
{title:"How to Start a Successful Blog in 2023",content:"Learn how to start a blog in less than an hour. Become a blogger today by following the steps we used when starting our blog, which now has reached more than 20 million people and has been seen in the New York Times, TIME magazine, and on the TODAY show.How to Become a Blogger in 5 Simple Steps?1]Choose your blog name and get your blog hosting.2]Start your blog by adding WordPress.3]Pick a simple theme to make your blog your own.4]Add two key blogging plugins to find your readers and track stats.5]Write compelling content to create a blog that your readers love.While there are other blogging platforms out there like Blogger.com and Tumblr.com, nearly every serious blogger uses a self-hosted WordPress site because of its creative freedom and flexibility.While there are other blogging platforms out there like Blogger.com and Tumblr.com, nearly every serious blogger uses a self-hosted WordPress site because of its creative freedom and flexibility."},
{title:"Top tips for a successful blog in generations", content:"With so many amazing arts, heritage and creative blogs out there already, how do you make yours stand out? How do you get people returning for more, and how do you increase visitor numbers and build a loyal following?.There are plenty of tips and tricks to get your own creative blog rocking and therefore raising your profile and helping you to win more business. Here's my top 20 tips for a successful creative blog, but if you can think of any more, please comment below and help me make this the best list on the web.Choosing a blogging platform is your first challenge. There are lots of free options out there including Wordpress, Tumblr, TypePad and Blogger. All of them offer free design themes so you can customise your own blog so it's very easy to get started. And if you're not sure how to use them? There's plenty of online video tutorials to be found on the web – for example, Wordpress has its very own lesson list to help get you started.Choosing a blogging platform is your first challenge. There are lots of free options out there including Wordpress, Tumblr, TypePad and Blogger. All of them offer free design themes so you can customise your own blog so it's very easy to get started. And if you're not sure how to use them? There's plenty of online video tutorials to be found on the web – for example, Wordpress has its very own lesson list to help get you started."},
{title:"Blog SEO: How to Search Engine Optimize Your Blog Content",content:"56% of surveyed consumers have made a purchase from a company after reading their blog and 10% of marketers who use blogging say it generates the biggest return on investment. If you're writing a blog for a business, those stats make blog SEO a pretty big deal.Even if you're blogging just for fun, SEO can help you boost your message and connect with more engaged readers.Even if you're blogging just for fun, SEO can help you boost your message and connect with more engaged readers.What is blog SEO?Blog SEO is the practice of creating and updating a blog to improve search engine rankings. This approach usually includes keyword research, link building, image optimization, and content writing.Blogging helps boost SEO quality by positioning your website as a relevant answer to your customers' questions. Blog posts that use a variety of on-page SEO tactics can give you more opportunities to rank in search engines and make your site more appealing to visitors.When you optimize your web pages — including your blog posts — you're making your website more visible to people who are using search engines (like Google) to find your product or service. But does your blog content really help your business organically rank on search engines?Google and other search engines use ranking factors to figure out what results come up for each search query. It also decides how to rank those results. Blogging can help you optimize your site for important Google ranking factors like:Organic Click-Through Rate,Index Coverage,Backlinks,Internal Links,"},
{title:"Tips and Tricks to Increase Blog Traffic",content:"Every blogger has experienced “blogging drought.” This is not an unusual scenario. In fact, majority of bloggers experience the lull of blogging and the scarcity of readers. You published a well-written blog post and expected (and hoped) others to see, read, share and comment on it. But instead, you got NOTHING. It would seem like no one is even paying attention to your blog, let alone read your article and share it. The blogging drought phenomenon happens all the time, particularly with company blogs. In most niches and industries, you’ll be in competition with hundreds (if not thousands) of other bloggers for the attention of a small pool of readers. To stand out from the rest, your blog must be truly one of a kind in all aspects – design, content, marketing, and so on. Making an exceptional blog that receives ample traffic requires serious time, dedication and money. Based on an article posted on the New York Times, roughly 95% of blogs fail. If you really want to make your blog work, you must beat this hostile statistic. The first thing you must focus on is increasing your blog traffic. The key here is to generate RELEVANT and TARGETED traffic. Receiving “more traffic” won’t always get you the results you want unless the increased traffic is pertinent to your brand and niche. Here are tried-and-tested tips and ideas on how to boost relevant traffic to your blog."}]);
  return (
    <div className="App">
      <BlogContext.Provider value={{blog,setBlog}}>
      <Routes>
      <Route path='/' element={<Home/>}/>
      <Route path="/createblog" element={<CreateBlog/>} />
      <Route path='/blogs'   element={<Blogs/>} />
      <Route path='/ReadBlogContent/:id'   element={<ReadBlogContent/>} />
      <Route path='/deletePost/:id'   element={<DeleteBlogs/>} />
      </Routes>
      </BlogContext.Provider>
    </div>
  );
}

export default App;
